/**
 * PARouteContainer.js
 * The wrapper container for PA
 *
 * @version 0.0.1
 * @author [Wilson](wilson@policypal.com)
 */
import window from 'global/window';
import _ from 'lodash';
import React, { Component, Fragment } from 'react';
import { Helmet } from 'react-helmet';
import { connect } from 'react-redux';
import { change, reset } from 'redux-form';
import { /* Prompt, */ withRouter } from 'react-router-dom';
import PropTypes from 'prop-types';
import Page from 'components/Page/Page';
import Loadable from 'react-loadable';

import { resetCompare } from '../../actions/compareActions';
import { getPromoGiveaways, resetInsuranceForm, FORMSTATES } from '../../actions/insuranceActions';
import { clearPAData } from '../../actions/paActions';
import { isProduction } from '../../lib/tagFunctions';

// import StepProgressBar from '../../components/Templates/StepProgressBar/StepProgressBar';
import RefreshTimer from '../../components/Templates/RefreshTimer/RefreshTimer';

// import PAMainContainer from './PAMainContainer';
// import PAChoosePlanContainer from './PAChoosePlanContainer';
// import PADetailContainer from './PADetailContainer';
// import PACheckoutContainer from './PACheckoutContainer';

import { dateToStoreString } from '../../lib/dateFunctions';
import { routeHandler } from '../../lib/helperFunctions';

const form = 'pa';
const detailFormName = 'pa-detail';

const StepProgressBar = Loadable({
  loader: () => import(/* webpackChunkName: "StepProgressBar" */ '../../components/Templates/StepProgressBar/StepProgressBar'),
  loading: () => null,
  modules: ['StepProgressBar']
});

const PAMainContainer = Loadable({
  loader: () => import(/* webpackChunkName: "PAMainContainer" */ './PAMainContainer'),
  loading: () => null,
  modules: ['PAMainContainer']
});

const PAChoosePlanContainer = Loadable({
  loader: () => import(/* webpackChunkName: "PAChoosePlanContainer" */ './PAChoosePlanContainer'),
  loading: () => null,
  modules: ['PAChoosePlanContainer']
});

const PADetailContainer = Loadable({
  loader: () => import(/* webpackChunkName: "PADetailContainer" */ './PADetailContainer'),
  loading: () => null,
  modules: ['PADetailContainer']
});

const PACheckoutContainer = Loadable({
  loader: () => import(/* webpackChunkName: "PACheckoutContainer" */ './PACheckoutContainer'),
  loading: () => null,
  modules: ['PACheckoutContainer']
});

class PetRouteContainer extends Component {
  static getDerivedStateFromProps(nextProps, prevState) {
    const { formIndex, isMounted } = prevState;
    const { formState, match, history } = nextProps;

    if (isMounted) {
      const routeResult = routeHandler('personal-accident', formState, formIndex, match, history);
      if (routeResult && routeResult.formIndex !== null) {
        return ({
          formIndex: routeResult.formIndex,
        });
      }
    }
    return null;
  }

  constructor(props) {
    super(props);

    const {
      resetStoreForm,
      paPromoGiveAway,
      clearData,
      history,
    } = this.props;

    this.state = {
      formIndex: 0,
      isInitialLoad: true,
    };

    // history.replace(`/personal-accident${history.location.search}`);

    clearData();
    this.props.resetCompare();
    this.props.resetInsuranceForm();

    resetStoreForm('pa');
    resetStoreForm('pa-detail');

    if (paPromoGiveAway === undefined) {
      this.props.getPromoGiveaways();
    }

    this.setInitialFormState = this.setInitialFormState.bind(this);
    this.renderStepProgress = this.renderStepProgress.bind(this);
    this.renderPAForms = this.renderPAForms.bind(this);
    this.changeUserDetails = this.changeUserDetails.bind(this);
  }

  componentDidMount() {
    // window.addEventListener('beforeunload', this.handleLeavePage);
    this.setInitialFormState();
    this.setState({
      // eslint-disable-next-line
      isMounted: true,
    });
  }

  componentDidUpdate(prevProps) {
    const { isInitialLoad } = this.state;
    const { userDetails } = this.props;

    if (isInitialLoad && _.isEqual(userDetails, prevProps.userDetails)) {
      this.changeUserDetails();
    }
  }

  componentWillUnmount() {
    const { clearData, resetStoreForm } = this.props;
    clearData();
    this.props.resetCompare();
    this.props.resetInsuranceForm();

    resetStoreForm('pa');
    resetStoreForm('pa-detail');
  }

  setInitialFormState() {
    const { changeForm } = this.props;

    const startDate = new Date();
    startDate.setDate(startDate.getDate() + 1);

    changeForm(form, 'my_occupation', 'Accountant');
    changeForm(form, 'start_date', dateToStoreString(startDate));

    this.changeUserDetails();
  }

  changeUserDetails() {
    const { isInitialLoad } = this.state;
    const { userDetails, changeForm } = this.props;

    if (userDetails) {
      const surname = userDetails && userDetails.surname ? userDetails.surname : '';
      const firstname = userDetails && userDetails.firstName ? ` ${userDetails.firstName}` : '';

      changeForm(detailFormName, 'email', userDetails.email);
      changeForm(detailFormName, 'name', `${surname}${firstname}`);
      changeForm(detailFormName, 'nationality', userDetails.nationality);
      changeForm(detailFormName, 'id_type', userDetails.id_type);
      changeForm(detailFormName, 'nric', userDetails.nric);
      changeForm(detailFormName, 'race', userDetails.race);
      changeForm(detailFormName, 'gender', _.startCase(userDetails.gender));
      changeForm(detailFormName, 'birth_date', userDetails.dateOfBirth);
      changeForm(detailFormName, 'marital_status', _.startCase(userDetails.marital_status));
      changeForm(detailFormName, 'occupation', userDetails.occupation);
      changeForm(detailFormName, 'mobile_number', userDetails.contact);
      changeForm(detailFormName, 'postal', userDetails.postal_code);

      if (isInitialLoad) {
        this.setState({
          isInitialLoad: false,
        });
      }
    }
  }

  renderStepProgress() {
    const { formIndex } = this.state;
    const { charged } = this.props;
    return (
      <StepProgressBar
        maxWidth="556px"
        formState={formIndex}
        inputList={[
          'Choose Plan',
          'Details',
          'Checkout',
        ]}
        form="pa"
        stateList={[
          FORMSTATES.CHOOSEPLAN,
          FORMSTATES.DETAILFORM,
          FORMSTATES.CHECKOUT,
        ]}
        stateDescriptions={[
          { main: ''/* , 'Choose your Plan', sub: 'Compare price plans from multiple companies' */ },
          { main: 'Details' },
          { main: 'Checkout' },
        ]}
        charged={charged}
      />
    );
  }

  renderPAForms() {
    const { formIndex } = this.state;
    const {
      userDetails,
      charged,
    } = this.props;

    const isLoggedIn = !_.isEmpty(userDetails);


    switch (formIndex) {
      case FORMSTATES.CHOOSEPLAN.index:
        return <PAChoosePlanContainer isLoggedIn={isLoggedIn} />;
      case FORMSTATES.DETAILFORM.index:
        return (
          <PADetailContainer
            isLoggedIn={isLoggedIn}
            piggyCallback={this.changeUserDetails}
          />
        );
      case FORMSTATES.CHECKOUT.index:
      case FORMSTATES.SUBMITTED.index:
        return <PACheckoutContainer isLoggedIn={isLoggedIn} charged={charged} />;
      default:
        return <PAMainContainer isLoggedIn={isLoggedIn} />;
    }
  }

  render() {
    const { paPromoGiveAway, constants } = this.props;

    if (!constants || paPromoGiveAway === undefined) {
      return null;
    }

    const meta = isProduction ? 'PolicyPal, personal accident insurance, singapore personal accident Insurance, PA insurance, accident insurance, accident insurance singapore, personal accident' : '';
    return (
      <Page id="pa">
        <Helmet>
          <meta name="keywords" content={meta} />
          <title>PolicyPal 2.0 - Personal Accident Insurance</title>
          <meta name="title" content="PolicyPal 2.0 - Personal Accident Insurance" />
          <meta name="description" content="Browse products for personal accident insurance." />
        </Helmet>
        {/* {
          formState !== FORMSTATES.MAIN ?
            <Prompt
              when={true}
              message="Are you sure you want to leave current page?"
            />
          :
            null
        } */}
        {this.renderStepProgress()}
        {this.renderPAForms()}
        <RefreshTimer ref={(node) => {
            if (!this.refresher) {
              node.invokeNewTimer();
            }
            this.refresher = node;
          }}
        />
      </Page>
    );
  }
}

PetRouteContainer.defaultProps = {
  userDetails: null,
  charged: null,
  paPromoGiveAway: undefined,
  constants: null,
};

PetRouteContainer.propTypes = {
  clearData: PropTypes.func.isRequired,
  resetCompare: PropTypes.func.isRequired,
  getPromoGiveaways: PropTypes.func.isRequired,
  resetInsuranceForm: PropTypes.func.isRequired,
  changeForm: PropTypes.func.isRequired,
  resetStoreForm: PropTypes.func.isRequired,
  userDetails: PropTypes.object,
  formState: PropTypes.number.isRequired,
  charged: PropTypes.bool,
  paPromoGiveAway: PropTypes.object,
  constants: PropTypes.object,
  history: PropTypes.object.isRequired,
};

const mapStateToProps = state => ({
  paPromoGiveAway: state.insurance.promoGiveaways.pa,
  formState: state.insurance.formState,
  constants: state.insurance.constants,
  charged: state.insurance.charged,
  userDetails: state.userDetails.data,
});

const mapDispatchToProps = dispatch => ({
  changeForm: (form, key, value) => (
    dispatch(change(form, key, value))
  ),
  resetInsuranceForm: () => (
    dispatch(resetInsuranceForm())
  ),
  resetStoreForm: form => (
    dispatch(reset(form))
  ),
  getPromoGiveaways: () => {
    dispatch(getPromoGiveaways('pa'));
  },
  resetCompare: () => {
    dispatch(resetCompare());
  },
  clearData: () => {
    dispatch(clearPAData());
  },
});

export default withRouter(connect(mapStateToProps, mapDispatchToProps)(PetRouteContainer));
